
      #app[data-booting] {
        visibility: hidden; /* Hide the app while its booting */
        width: 100%;
        height: 100%;
        /* While app is booting, layout might be broken, prevent scrollbar with overflow hidden */
        overflow: hidden;
      }

      #splashscreen {
        position: fixed;
        z-index: 1000;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: black;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        opacity: 0;
        animation-name: splashin;
        animation-duration: 0.4s;
        animation-fill-mode: forwards;
      }
      @keyframes splashin {
        from {
          opacity: 0;
          /* transform: scale(0); */
        }

        to {
          opacity: 1;
          /* transform: scale(1); */
        }
      }

      #splashscreen[data-booting-done] {
        animation-delay: 0;
        animation-duration: 0.3s;
        animation-name: splashout;
        animation-fill-mode: forwards;
      }
      @keyframes splashout {
        from {
          opacity: 1;
        }

        to {
          opacity: 0;
          display: none;
        }
      }

      #splashscreen_content {
        display: flex;
        justify-content: center;
        flex: 1;
        width: 80%;
      }

      #boot_runtime_error {
        display: block;
      }

      #boot_runtime_error details {
        max-width: 500px;
      }

      #boot_runtime_error details pre {
        overflow: auto;
      }
    